import delay from '../../common/delay';
import { generateTrackerForPortal } from '../rest/SaaspassExtensionResource';

const portalImportPasswordManager = async () => {
  const path = await generateTrackerForPortal();
  const tab = await browser.tabs.create({ url: `${api.baseURL}/#${path}` });

  await delay(4000);
  const url = `${api.baseURL}/#/public-user-accounts/import`;
  await browser.tabs.update(tab.id, { url });
};

export default portalImportPasswordManager;
